#webdev-info {
    position: relative;
    width: 100%;
    min-height: 50em;
    animation: fadeIn 1.7s;
}

#about-content {
    animation: fadeIn 1.7s;
    padding-top: 4em;
}

#about-content>h1 {
    padding-bottom: 0.5em;
}

#qualities-info {
    min-height: 20em;
    position: relative;
    box-sizing: border-box;
    flex-wrap: wrap;
    display: flex;
    animation: fadeIn 3s;
    justify-content: center;
    margin-top: 2em;
    width: 50%;
    margin-bottom: 3em;
}

.hexagon-wrapped {
    min-height: 100%;
    flex: 20%;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 2em 1em 1em;
    animation: pop-in 2s cubic-bezier(.41, -2, .66, 3) forwards;
}

.hexagon-wrapped>h4 {
    margin: 3em 0 0.5em;
}

.hexagon-wrapped>p {
    text-align: center;
    width: 100%;
}

.hexagon {
    border-radius: 1px;
    margin: 0 auto;
    width: 100px;
    height: 57.735px;
    background: #8bffe2;
    position: relative;
    text-align: center;
}

.hexagon::before {
    content: "";
    position: absolute;
    top: -28.5px;
    left: 0;
    width: 0;
    height: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-bottom: 28.8675px solid #8bffe2;
    border-radius: 2px;
}

.hexagon::after {
    content: "";
    position: absolute;
    bottom: -28px;
    left: 0;
    width: 0;
    height: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-top: 28.8675px solid #8bffe2;
    border-radius: 2px;
}

.hexagon i {
    margin-top: 5px;
    font-size: 2.5em;
    color: rgba(0, 0, 0, 0.698);
}

#technologies {
    margin-bottom: 5em;
}

#perc-graphic {
    width: 70%;
    background-color: #13141434;
    list-style: none;
}

#perc-graphic>li {
    display: flex;
    position: relative;
    height: 2.5em;
    background-color: #13141457;
    border-bottom: rgba(255, 255, 255, 0.123) solid 2px;
    align-items: center;
}


#perc-graphic>li>span {
    position: absolute;
    padding-left: 0.5em;
}

.language-item {
    position: relative;
    width: 100%;
    margin-left: 10em;
    height: 80%;
    border: black solid 1.5px;
    background-color: rgba(0, 0, 0, 0.657);
    margin-right: 1em;
    padding-left: 0.2em;
}

.percentage {
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    text-align: center;
    width: 100%;
    height: 80%;
    color: white;
    animation: fadeSlow ease-in 5.25s;
}

#html-lvl {
    width: 90%;
    animation: growSlowHTML 4s;
}

#css-lvl {
    width: 90%;
    animation: growSlowCSS 4s;
}

#js-lvl {
    width: 85%;
    animation: growSlowJS 4s;
}

#sql-lvl {
    width: 80%;
    animation: growSlowSQL 4s;
}

#csharp-lvl {
    width: 75%;
    animation: growSlowCSharp 4s;
}

#react-lvl {
    width: 70%;
    animation: growSlowReact 4s;
}

#node-lvl {
    width: 70%;
    animation: growSlowNode 4s;
}

#cplus-lvl {
    width: 65%;
    animation: growSlowCPlus 4s;
}

#c-lvl {
    width: 65%;
    animation: growSlowC 4s;
}

#vue-lvl {
    width: 65%;
    animation: growSlowVue 4s;
}

#golang-lvl {
    width: 65%;
    animation: growSlowGolang 4s;
}

#azure-lvl {
    width: 65%;
    animation: growSlowAzure 4s;
}

#docker-lvl {
    width: 60%;
    animation: growSlowDocker 4s;
}

#python-lvl {
    width: 60%;
    animation: growSlowPython 4s;
}

#java-lvl {
    width: 60%;
    animation: growSlowJava 4s;
}

#mongodb-lvl {
    width: 50%;
    animation: growSlowMongoDB 4s;
}

.language-lvl {
    position: absolute;
    background-color: #8bffe2b6;
    height: 80%;
    margin: auto;
    top: 0;
    bottom: 0;
}

#technologies>h3 {
    margin-bottom: 1.5em;
}

@media screen and (max-width: 990px) {
    .hexagon::before {
        top: -28px;
    }

    #about-content>p {
        width: 100%;
    }

    #qualities-info {
        width: 100%;
    }

    #perc-graphic {
        width: 100%;
    }
}

@keyframes fadeSlow {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0;
    }

    75% {
        opacity: 0.25;
    }

    100% {
        opacity: 1;
    }
}

@keyframes pop-in {
    0% {
        opacity: 0;
        transform: translateY(-4rem) scale(.7);
    }

    100% {
        opacity: 1;
        transform: none;
    }
}


